import { Avatar, Card, CardContent, Typography } from "@mui/material";
import React from "react";
import Grid from "@mui/material/Unstable_Grid2";

interface PodiumColumnProps {
  height: string;
}

const PodiumColumn: React.FC<PodiumColumnProps> = ({
  height
}: PodiumColumnProps) => <Card style={{ height }} />;

interface PodiumProps {
  title: string;
  firstPlaceName: string;
  secondPlaceName: string;
  thirdPlaceName: string;
}

const Podium: React.FC<PodiumProps> = ({
  title,
  firstPlaceName,
  secondPlaceName,
  thirdPlaceName
}: PodiumProps) => (
  <Grid key={title} container spacing={2} style={{ marginTop: "10px" }}>
    <Grid xs={3}>
      <Card style={{ height: "100%", backgroundColor: "#394473" }}>
        <CardContent>
          <Typography
            variant="h5"
            style={{ color: "white", textAlign: "center" }}
          >
            {title}
          </Typography>
        </CardContent>
      </Card>
    </Grid>
    <Grid style={{ marginTop: "auto" }} xs={3}>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center"
        }}
      >
        <Avatar alt="Remy Sharp" />
        <Typography
          color="textPrimary"
          style={{ fontWeight: 700, textAlign: "center" }}
        >
          2. {secondPlaceName}
        </Typography>
      </div>
      <PodiumColumn height="100px" />
    </Grid>
    <Grid xs={3}>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center"
        }}
      >
        <Avatar alt="Remy Sharp" />

        <Typography
          color="textPrimary"
          style={{ fontWeight: 700, textAlign: "center" }}
        >
          1. {firstPlaceName}
        </Typography>
      </div>
      <PodiumColumn height="150px" />
    </Grid>
    <Grid style={{ marginTop: "auto" }} xs={3}>
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center"
        }}
      >
        <Avatar alt="Remy Sharp" />
        <Typography
          color="textPrimary"
          style={{ fontWeight: 700, textAlign: "center" }}
        >
          3. {thirdPlaceName}
        </Typography>
      </div>
      <PodiumColumn height="50px" />
    </Grid>
  </Grid>
);

export default Podium;
